//引入图标组件
import logobg from "@/components/login/loginbg.vue"
export default {
	data() {
		return {
			bool:false,
			countdowntext:"验证码",
			wait:60,
			disabled:true,
			username:"",
			phone:"",
			times:null
		}
	},
	methods: {
		//当失去焦点的时候
		Losefocus(){
			this.bool = true
		},













































		//这是点击跳转到短信登录
		smslogin(url){
			uni.navigateTo({
				url
			})
		},
		//封装一个提示框的方法
		toast(message){
			uni.showToast({
				title:message,
				icon:"none"
			})
		},
		//封装一个app和微信端 不同的登录请求方法
		Ordinarydifferentlogin(data,username,password){
			uni.request({
				url:"http://hbk.huiboke.com/api/login_and_register/userLogin",
				method:"POST",
				data,
				success:(res)=>{
					// console.log(res)
					if(res.data.code==0){
						this.toast("登录成功")
						//就将用户的昵称密码存储起来
						uni.setStorage({
							key:"userlogininfo",
							data:[{username,password}],
							success(res){
								uni.switchTab({
									url:"/pages/index/index"
								})
							}
						})
					}else{
						this.toast("验证码错误")
					}
				},
				fail:(err)=>{
					this.toast("登录失败")
				}
			})
		},
		//当用户点击的button
		ordinarylogin(e){
			let {username,password,phone,sms} = e.detail.value
			//写两个正则
			//这是验证账号
			let regusername = /^[\W|\w]{5,100}$/;
			//这是验证密码
			let reguserpassword = /^\w{6,16}$/;


















				//这是微信端
				uni.getStorage({
					key:"wxcodekey",
					success:(res)=>{
						if(username.match(regusername) && password.match(reguserpassword)){
							let data = {
								login_type:"weixin",
								username:username,
								password:password,
								opened:res.data
							}
							this.Ordinarydifferentlogin(data,username,password)
						}else{
							this.toast("请填写完整信息")
						}
					}
				})

		},
		//这是封装点击验证码的倒计时
		time(){
			this.disabled = true
			//这块点击反复执行定时器
			// clearInterval(times)
			let {countdowntext,wait} = this.$data
			// console.log(countdowntext,wait)
				this.times = setInterval(()=>{
					wait--
					// console.log(wait)
					this.countdowntext = wait
					if(wait==0){
						this.disabled = false
						countdowntext = "重新获取验证码"
						clearInterval(this.times)
						this.countdowntext = countdowntext
						this.wait = 60
					}
					
				},1000)
		}
	},
	components:{
		logobg
	}
}
